import { useState, useRef } from "react"; 
import "./App.css";

function App() { 
  const inputRef = useRef(null); 
  const [result, setResult] = useState(0);

  function handleInput(e) {
    inputRef.current.value += e.target.value;
  }

  function calculate(e) {
    e.preventDefault();
    try {
      setResult(eval(inputRef.current.value)); // Evaluate the input string
    } catch {
      alert("Invalid Input");
    }
  }

  function resetAll(e) {
    e.preventDefault();
    inputRef.current.value = "";
    setResult(0);
  }

  return (
    <div className="App">
      <div className="calculator">
        <div className="display">
          <p className="result">{result}</p>
          <input ref={inputRef} type="text" className="input-field" disabled />
        </div>
        <div className="buttons">
          <button onClick={resetAll} className="button btn-function">AC</button>
          <button value="/" onClick={handleInput} className="button btn-function"></button>
          <button value="*" onClick={handleInput} className="button btn-function"></button>
          <button value="-" onClick={handleInput} className="button btn-function"></button>

          <button value="7" onClick={handleInput} className="button">7</button>
          <button value="8" onClick={handleInput} className="button">8</button>
          <button value="9" onClick={handleInput} className="button">9</button>
          <button value="+" onClick={handleInput} className="button btn-function">+</button>

          <button value="4" onClick={handleInput} className="button">4</button>
          <button value="5" onClick={handleInput} className="button">5</button>
          <button value="6" onClick={handleInput} className="button">6</button>
          <button onClick={calculate} className="button btn-function equal">=</button>

          <button value="1" onClick={handleInput} className="button">1</button>
          <button value="2" onClick={handleInput} className="button">2</button>
          <button value="3" onClick={handleInput} className="button">3</button>

          <button value="0" onClick={handleInput} className="button zero">0</button>
          <button value="." onClick={handleInput} className="button">.</button>
        </div>
      </div>
    </div>
  );
}

export default App;
